  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 34px;
    height: 2px;
    margin-top: -1px;
    margin-left: -17px;
    background-color: #1a1a1a; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: all .2s; }
  .sh-burger span::before {
    transform: translateY(-12px); }
  .sh-burger span::after {
    transform: translateY(12px); }
  .sh-burger:hover {
    opacity: .7; }
  .sh-burger--active span {
    height: 0; }
    .sh-burger--active span::before {
      transform: rotate(45deg);
      background-color: #ff0202; }
    .sh-burger--active span::after {
      transform: rotate(-45deg);
      background-color: #ff0202; }

