.message {
	padding-top: 54px;
	padding-bottom: 50px;
	background-color: #f4f4f4;

	&__container {}

	&__title {
		color: #1e1e1e;
		font-family: "Open Sans";
		font-size: 24px;
		font-weight: 700;
		text-align: center;
		margin-bottom: 43px;

		@media screen and (max-width: 800px) {
			font-size: 20px;
		}
	}

	&__form {
		padding: 0 18%;

		@media screen and (max-width: 500px) {
			padding: 0;
		}
	}

	&__form-label {
		display: flex;
		align-items: center;
		justify-content: center;

		&:not(:last-child) {
			margin-bottom: 20px;

			@media screen and (max-width: 800px) {
				margin-bottom: 5px;
			}
		}

		@media screen and (max-width: 800px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__form-name {
		flex: 0 0 auto;
		width: 105px;
		margin-right: 18px;

		@media screen and (max-width: 800px) {
			margin-right: 0;
			margin-bottom: 5px;
		}
	}

	&__form-input {
		width: 100%;
		padding: 10px;
		background-color: #fafafa;
		transition-duration: .2s;

		&:focus {
			background-color: #fff;
		}
		&[aria-invalid="true"]{border: 1px solid red;}
		@media screen and (max-width: 800px) {
			margin-right: 0;
			margin-bottom: 5px;
		}
	}

	&__form-tel {
		flex: 0 0 auto;
		width: 105px;
		margin-right: 18px;

		@media screen and (max-width: 800px) {
			margin-right: 0;
			margin-bottom: 5px;
		}
	}

	&__form-mail {
		flex: 0 0 auto;
		width: 105px;
		margin-right: 18px;

		@media screen and (max-width: 800px) {
			margin-right: 0;
			margin-bottom: 5px;
		}
	}

	&__form-message {
		flex: 0 0 auto;
		width: 105px;
		margin-right: 18px;
		align-self: flex-start;

		@media screen and (max-width: 800px) {
			margin-right: 0;
			margin-bottom: 15px;
		}
	}

	&__form-textarea {
		width: 100%;
		height: 140px;
		background-color: #fafafa;
		transition-duration: .2s;
		padding: 10px;

		&:focus {
			background-color: #fff;
		}
		&[aria-invalid="true"]{border: 1px solid red;}
	}

	&__form-button {
		display: inline-flex;
		font-family: "Open Sans";
		font-size: 16px;
		font-weight: 700;
		color: #ff0000;
		background-color: #fff;
		cursor: pointer;
		padding: 15px 18px;
		transition: 0.3s;
		border: 1px solid #ff0000;
		margin-left: 15px;

		&:hover {
			color: #ffffff;
			background-color: #ff0000;
		}

		&-wrapper {
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			margin-bottom: 33px;

			@media screen and (max-width: 950px) {
				flex-direction: column;
			}
		}

		&-text {
			text-align: right;
			color: #ff0000;
			font-size: 16px;

			@media screen and (max-width: 950px) {
				margin-bottom: 16px;
			}
		}
	}

	&__line {
		background-color: #dedede;
		height: 2px;
		margin-bottom: 31px;
	}

	&__info {
		color: #1b1b1b;
		font-family: "Open Sans";
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		padding: 0 18%;

		@media screen and (max-width: 500px) {
			padding: 0;
		}
	}
}